import logo from './logo.svg';
import {useState, useRef, useEffect} from 'react'
import './App.css';
import AudiotrackIcon from '@material-ui/icons/Audiotrack';
import PlayArrowIcon from '@material-ui/icons/PlayArrow';
import PauseIcon from '@material-ui/icons/Pause';
import AddIcon from '@material-ui/icons/Add';
import Draggable from 'react-draggable';
import { v4 as uuidv4 } from 'uuid';

const defaultInfo = {
	lastModified: 0,
  lastModifiedDate: null,
  name: "filename",
  size: 0,
  type: null,
  webkitRelativePath: "",
	title: "",
	length: 0,
	artists: [],
	genres: [],
	groups: []
}

function FileUploadPage({pageHeight, pageWidth, volPosition, id, startingX, startingY, handleDelete}){
	const [selectedFile, setSelectedFile] = useState();
	const [fileInfo, setFileInfo] = useState(defaultInfo)
	const [isFilePicked, setIsFilePicked] = useState(false);
	const [isPlaying, setIsPlaying] = useState(false);
	const [pausedTime, setPausedTime] = useState(0);
	const [position, setPosition] = useState({ x: startingX, y: startingY });
	const myRef = useRef(null)


	useEffect(() => {

		calculateVolume()
	}, [volPosition, position])

	function calculateVolume() {
		let min = 0
		let max = pageHeight + pageWidth
		let x, y = 0

		if ((position.x < 0 && volPosition.x > 0) || (volPosition.x < 0 && position.x > 0)) {
			x = position.x + volPosition.x
		} else {
			x = position.x - volPosition.x
		}

		if ((position.y < 0 && volPosition.y > 0) || (volPosition.y < 0 && position.y > 0)) {
			y = position.y + volPosition.y
		} else {
			y = position.y - volPosition.y
		}
		
		let vol = 1 - ((Math.abs(x) + Math.abs(y))*2) / max
		if (vol > 1) vol = 1
		if (vol < 0) vol = 0
		if (myRef !== null && myRef.current !== null) {
			myRef.current.volume = vol
		}

	}

	const changeHandler = (event) => {
		setSelectedFile(event.target.files[0]);
		setIsFilePicked(true);
		setFileInfo(prevState => ({...prevState, ...event.target.files[0]}))
	};

	const playAudio = () => {
		myRef.current.src = window.URL.createObjectURL(selectedFile)
		myRef.current.play()
		calculateVolume() 
		setIsPlaying(prevState => !prevState)
  }

	const pauseAudio = () => {
		if (isPlaying) {
			myRef.current.pause()
			setPausedTime(myRef.current.currentTime)
		} else {
			myRef.current.currentTime = pausedTime
			myRef.current.play()
			calculateVolume() 
		}
		setIsPlaying(prevState => !prevState)
	}

	const trackPos = (data) => {
    setPosition({ x: data.x, y: data.y });
	};

	

	return(
		<Draggable onDrag={(e, data) => trackPos(data)} defaultPosition={{x: startingX, y: startingY}}>
			<div className="musicBox">
				<div className="closeContainer">
					{/* <div className="closeButton" onClick={e => handleDelete(e, id)}>X</div> */}
				</div>
				{isFilePicked ? (
					<>
						<div className="boxWindow">
							{selectedFile.type === "audio/mpeg" ? <AudiotrackIcon /> : null}
						</div>
						<div>
							<button onClick={playAudio}>
								<PlayArrowIcon />
							</button>
							<button onClick={pauseAudio}>
								<PauseIcon />
							</button>
							
							<audio ref={myRef} id="audio-element" src="" type="audio/mp3" />
						</div>
					</>
				) : (
					<div className="boxWindow">
						<label htmlFor="file" className="btn">Add<AddIcon /></label>
						<input id="file" type="file" name="file" onChange={changeHandler} style={{visibility: "hidden"}}/>
					</div>
				)}
				{isFilePicked ? <p className="songName">{selectedFile.name}</p> : null}
			</div>
		</Draggable>
	)
}

function App() {
	const [playBoxes, setPlayBoxes] = useState([])
	const [pageWidth, setPageWidth] = useState(0)
	const [pageHeight, setPageHeight] = useState(0)	
	const [volPosition, setVolPosition] = useState({ x: 0, y: 0 });

	useEffect(() => {
		setPageHeight(window.innerHeight)
		setPageWidth(window.innerWidth)
		setVolPosition({ x: window.innerHeight/2, y: window.innerWidth/2 })
	}, [])

	const trackPos = (data) => {
    setVolPosition({ x: data.x, y: data.y });
	};

	function handleAddBox(e) {
		setPlayBoxes(prevState => [...prevState, {
			id: uuidv4(),
      title: "",
      startingX: e.clientX,
      startingY: e.clientY,
		}])
	}

	function handleDelete(e, id) {
		e.stopPropagation()
		e.preventDefault()
		setPlayBoxes(prevState => prevState.filter(box => box.id !== id))
	}


	function displayPlayBoxes() {
		return playBoxes.map(comp => {
			return <FileUploadPage 
							pageHeight={pageHeight}
							pageWidth={pageWidth}
							volPosition={volPosition} 
							key={comp.id} 
							handleDelete={handleDelete} 
							{...comp}
						/>
		})
	}

	
  return (
    <div className="App">
			<div onClick={handleAddBox}>
				<span>Add Music Box</span>
			</div>
			{displayPlayBoxes()}
			<Draggable onDrag={(e, data) => trackPos(data)} defaultPosition={{x: window.innerWidth/2, y: window.innerHeight/2}}>
				<div className="Vol" style={{position: 'absolute'}}>Vol</div>
			</Draggable>
    </div>
  );
}

export default App;